@mixin defaultTextStyles {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-family: $siemensFontRegular;
    font-weight: normal;
    text-rendering: optimizeLegibility;
}

@mixin headline1 {
    font-family: $siemensFontBlack;
    font-size: 36px;
    font-size: rem(36px);
    line-height: 43px;
    line-height: rem(43px);
    margin-bottom: 20px;
    margin-bottom: rem(20px);
    margin-top: 0;
}

@mixin headline2 {
    font-family: $siemensFontBold;
    font-size: 31px;
    font-size: rem(31px);
    line-height: 43px;
    line-height: rem(43px);
    margin-bottom: 8px;
    margin-bottom: rem(8px);
    margin-top: 0;
}

@mixin headline3 {
    font-family: $siemensFontRegular;
    font-size: 26px;
    font-size: rem(26px);
    line-height: 36px;
    line-height: rem(36px);
    margin-bottom: 8px;
    margin-bottom: rem(8px);
    margin-top: 0;
}

@mixin headline4 {
    font-family: $siemensFontRegular;
    font-size: 21px;
    font-size: rem(21px);
    line-height: 29px;
    line-height: rem(29px);
    margin-bottom: 4px;
    margin-bottom: rem(4px);
    margin-top: 0;
}

@mixin headline5 {
    font-family: $siemensFontRegular;
    font-size: 17px;
    font-size: rem(17px);
    line-height: 23px;
    line-height: rem(23px);
    margin-bottom: 4px;
    margin-bottom: rem(4px);
    margin-top: 0;
}

@mixin headline6 {
    font-family: $siemensFontRegular;
    font-size: 16px;
    font-size: rem(16px);
    line-height: 22px;
    line-height: rem(22px);
    margin-bottom: 4px;
    margin-bottom: rem(4px);
    margin-top: 0;
}

@mixin paragraph {
    font-size: 14px;
    font-size: rem(14px);
    line-height: 20px;
    line-height: rem(20px);
    margin-bottom: 16px;
    margin-bottom: rem(16px);
    margin-top: 0;

    &.p--s {
        font-size: 12px;
        font-size: rem(12px);
        line-height: 17px;
        line-height: rem(17px);
    }
}

@mixin small {
    font-size: 12px;
    font-size: rem(12px);
    line-height: 16px;
    line-height: rem(16px);
}

@mixin em {
    // special font face that is already italic is used
    font-family: $siemensFontItalic;
    font-style: normal;
}

@mixin boldem {
    font-family: $siemensFontBoldItalic;
    font-style: normal;
}

@mixin blackem {
    font-family: $siemensFontBlackItalic;
    font-style: normal;
}

@mixin mark {
    background-color: $stateBlue;
}

@mixin strong {
    font-family: $siemensFontBold;
}

.h1 {
    @include defaultTextStyles;
    @include headline1;
}

.h2 {
    @include defaultTextStyles;
    @include headline2;
}

.h3 {
    @include defaultTextStyles;
    @include headline3;
}

.h4 {
    @include defaultTextStyles;
    @include headline4;
}

.h5 {
    @include defaultTextStyles;
    @include headline5;
}

.h6 {
    @include defaultTextStyles;
    @include headline6;
}

.subheadline {
    @include defaultTextStyles;
    color: $gray400;
    font-size: 17px;
    font-size: rem(17px);
    line-height: 25px;
    line-height: rem(25px);
}

.p {
    @include defaultTextStyles;
    @include paragraph;
}

.b,
.strong {
    @include defaultTextStyles;
    @include strong;
}

.small {
    @include defaultTextStyles;
    @include small;
}

.i,
.em {
    @include defaultTextStyles;
    @include em;
}

.i .b,
.b .i,
.strong .i,
.i .strong,
.em .b,
.b .em,
.strong .em,
.em .strong {
    @include boldem;
}

.h1 {
    .em, .i {
        @include blackem;
    }
}

.h2 {
    .em, .i {
        @include boldem;
    }
}

.mark {
    @include defaultTextStyles;
    @include mark;
}

//removeIf(sassHandover)
// style native elements when forced defaults
&.#{$forceDefaultClass} {
    & {
        @include defaultTextStyles;
    }

    h1 {
        @include defaultTextStyles;
        @include headline1;
    }

    h2 {
        @include defaultTextStyles;
        @include headline2;
    }

    h3 {
        @include defaultTextStyles;
        @include headline3;
    }

    h4 {
        @include defaultTextStyles;
        @include headline4;
    }

    h5 {
        @include defaultTextStyles;
        @include headline5;
    }

    h6 {
        @include defaultTextStyles;
        @include headline6;
    }

    p {
        @include defaultTextStyles;
        @include paragraph;
    }

    b, strong {
        @include defaultTextStyles;
        @include strong;
    }

    small {
        @include defaultTextStyles;
        @include small;
    }

    i, em {
        @include defaultTextStyles;
        @include em;
    }

    i b,
    b i,
    strong i,
    i strong,
    em b,
    b em,
    strong em,
    em strong {
        @include boldem;
    }

    h1 {
        em, i {
            @include blackem;
        }
    }

    h2 {
        em, i {
            @include boldem;
        }
    }

    mark {
        @include defaultTextStyles;
        @include mark;
    }

    a, a:visited {
        color: $stateBlue100;
        text-decoration: underline;
    }

    a:hover,
    a:focus {
        text-decoration: underline;
        color: $coolBlue;
    }

    a:active {
        color: $stateBlue250;
    }

    hr {
        border-top: 1px solid $gray200;
    }

    ul, ol {
        @include defaultTextStyles;
        line-height: 21px;
        line-height: rem(21px);
        margin-block-start: 1em;
        margin-block-end: 1em;
        padding-left: 22px;
        padding-left: rem(22px);

        ul,
        ol {
            padding-left: 32px;
            padding-left: rem(32px);
            margin-top: 12px;
            margin-top: rem(12px);
            margin-bottom: 12px;
            margin-bottom: rem(12px);
        }

        li {
            margin: 0 0 12px;
            margin: rem(0 0 12px);
        }
    }

    dl {
        @include defaultTextStyles;
        margin: 0;

        dt {
            font-family: $siemensFontBold;
            font-size: 17px;
            font-size: rem(17px);
            line-height: 23px;
            line-height: rem(23px);
            margin: 0;
            padding-bottom: 12px;
            padding-bottom: rem(12px);
        }

        dd {
            font-family: $siemensFontRegular;
            font-size: 14px;
            font-size: rem(14px);
            line-height: 20px;
            line-height: rem(20px);
            margin: 0;
            padding-bottom: 32px;
            padding-bottom: rem(32px);
        }

        &.dl--inlineList {
            dt {
                float: left;
                width: 33%;
            }

            dd {
                margin: 0 0 0 35%;
                padding: 0;

                + dd {
                    padding: 12px 0 0 0;
                    padding: rem(12px 0 0 0);
                }

                &:last-child {
                    padding-bottom: 32px;
                    padding-bottom: rem(32px);
                }
            }

            dd + dt {
                padding-top: 32px;
                padding-top: rem(32px);

                + dd {
                    padding-top: 32px;
                    padding-top: rem(32px);
                }
            }
        }
    }
}
//endRemoveIf(sassHandover)