.contentHeader {
    display: flex;
    flex-wrap: wrap-reverse;
    justify-content: space-between;

    .contentHeader__actions {
        margin-bottom: 8px;
        margin-bottom: rem(8px);
    }

    .header__headline {
        margin: 0;
    }

    .header__subline {
        color: $gray400;
        font-size: 17px;
        font-size: rem(17px);
        line-height: 25px;
        line-height: rem(25px);
    }

    .contentHeader__header {
        .button--secondary {
            font-size: 15px;
            font-size: rem(15px);
            line-height: 16px;
            line-height: rem(16px);
            margin: 0 4px 4px 0;
            padding: 3px 8px 3px 0;

            // temporary fix for wrong back button icon in content header
            // will be replaced by correctly sized new icon
            .iconMdsp {
                margin: 0;
                font-size: 12px;
                margin-left: 4px;
            }
        }
    }
}
