.inputGroup {
    margin-bottom: 16px;
    &__label {
        font-size: 14px;
        font-size: rem(14px);
        font-family: $siemensFontBold;
        line-height: 22px; // to accomodate with margin between input
        line-height: rem(22px);
        width: 100%;
        max-width: 100%;
        box-sizing: border-box;
        overflow: hidden;
    }

    .is-required:after {
        color: $functionalRed;
        content: "*";
        font-family: $siemensFontBold;
    }

    // input text fields
    input[type=text],
    input[type=number],
    input[type=password],
    &__textInput,
    &__textarea {
        box-shadow: 0 1px 2px 0 rgba(0,0,0, 0.24) inset;
        border-radius: 3px;
        border: 1px solid $gray400;
        box-sizing: border-box;
        color: $gray800;
        display: block;
        font-family: $siemensFontRegular;
        font-size: 14px;
        font-size: rem(14px);
        line-height: 16px;
        line-height: rem(16px);
        max-width: 100%;
        overflow: hidden;
        padding: 7px 12px;
        padding: rem(7px 12px);
        width: 100%;

        // pseudo-element placeholder:  must not be set in one assignent, does not work
        &::-webkit-input-placeholder {
            color: $gray400;
            opacity: 1;
        }
        &::-moz-placeholder {
            color: $gray400;
            opacity: 1;
        }
        &:-ms-input-placeholder {
            color: $gray400;
            opacity: 1;
        }

        &:focus, &.is-focused {
            box-shadow: 0 0 0px 2px $coolBlue;
            outline: none;
        }

        &[readonly], &.is-readonly {
            background-color: $gray100;
            border: 1px solid $gray200;
            box-shadow: none;
            color: $gray800;
        }

        &[disabled], &.is-disabled {
            background-color: #FFF;
            border: 1px solid $gray200;
            box-shadow: none;
            color: $gray400;
            &:hover {
                cursor: not-allowed;
            }
        }

        &.is-invalid,
        &.ng-invalid.ng-dirty,
        &.ng-invalid.ng-touched {
            border: 1px solid $functionalRed;
            color: $contextRed500;
            &:focus {
                box-shadow: 0 1px 2px 0 rgba(0,0,0, 0.24) inset;
            }
        }
    }

    // radio buttons and checkboxes
    .radioButtonWrapper,
    .checkboxWrapper {
        @include defaultMargin(8px, 0);
        position: relative;

        .checkbox__description,
        .radioButton__description {
            display: block;
            margin-left: 27px;
            margin-left: rem(27px);
        }
    }
    &__radioButton,
    input[type=radio] {
        position: absolute;
        opacity: 0;
        + label {
            font-size: 14px;
            font-size: rem(14px);
            color: $gray800;
            display: inline-block;
            margin-left: 26px;
            text-indent: -26px;
            &:hover {
                cursor: pointer;
            }
            &:before {
                background: $white;
                border-radius: 100%;
                box-shadow: 0 1px 2px 0 rgba(0,0,0,0.24) inset;
                border: 1px solid $gray400;
                content: '';
                cursor: pointer;
                display: inline-block;
                height: 14px;
                height: rem(14px);
                margin-right: 10px;
                position: relative;
                text-align: center;
                top: 0.15em;
                vertical-align: top;
                width: 14px;
                width: rem(14px);
                transition: all 250ms ease-in-out;
            }
        }

        &:focus {
            + label {
                &:before {
                    border-color: $stateBlue100;
                    box-shadow: 0 0px 2px 2px rgba($stateBlue100, 1);
                    outline: none;
                }
            }
        }
        &:checked {
            + label {
                &:before {
                    background-color: $white;
                    border: 1px solid $stateBlue100;
                    box-shadow: 0 0px 0px 3px rgba($stateBlue100, 1) inset;
                }
            }
            &[disabled],
            &.is-disabled {
                + label:before {
                    background-color: $white;
                    border: 1px solid $gray250;
                    box-shadow: 0 0px 0px 3px rgba($gray250, 0.7) inset;
                }
            }
        }

        &:disabled,
        &.is-disabled {
            cursor: not-allowed;
            + label {
                color: $gray200;
                cursor: not-allowed;
                &:before {
                    background: $gray100;
                    border-color: $gray200;
                    box-shadow: none;
                    cursor: not-allowed;
                }
                + label.radioButton__description {
                    color: $gray200;
                    cursor: not-allowed;
                }
            }
        }
        + label {
            &:empty {
                &:before {
                    margin-right: 0;
                }
            }
        }
    }

    .inputGroup__checkbox,
    input[type=checkbox]:not(.switch__checkbox) {
         position: absolute;
         opacity: 0;
         + label {
             color: $gray800;
             display: inline-block;
             font-size: 14px;
             font-size: rem(14px);
             margin-left: 26px;
             text-indent: -26px;
             &:hover {
                 cursor: pointer;
             }
             &:before {
                 background: $white;
                 border-radius: 2px;
                 border: 1px solid $gray400;
                 box-shadow: 0 1px 2px 0 rgba(0,0,0,0.24) inset;
                 content: '';
                 cursor: pointer;
                 display: inline-block;
                 height: 14px;
                 height: rem(14px);
                 margin-right: 10px;
                 position: relative;
                 text-align: center;
                 transition: all 250ms ease-in-out;
                 top: 0.15em;
                 vertical-align: top;
                 width: 14px;
                 width: rem(14px);
             }
         }
         &:checked {
             + label {
                 position: relative;
                 &:before {
                     background-color: $stateBlue100;
                     border: 1px solid $stateBlue100;
                 }
                 &:after {
                     border: solid $white;
                     border-width: 0 2px 2px 0;
                     content: '';
                     display: block;
                     height: 9px;
                     height: rem(9px);
                     left: -21px;
                     position: absolute;
                     top: 3px;
                     top: rem(3px);
                     transform: rotate(45deg);
                     width: 4px;
                     width: rem(4px);
                 }
             }
             &[disabled],
             &.is-disabled {
                 + label:after {
                     border-color: $gray250;
                 }
             }
         }

         &:focus {
             + label {
                 &:before {
                     border-color: $stateBlue100;
                     box-shadow: 0 0px 2px 0px rgba($stateBlue100, 1);
                     outline: none;
                 }
             }
         }

         &:disabled,
         &.is-disabled {
             cursor: not-allowed;
             + label {
                 color: $gray200;
                 cursor: not-allowed;
                 &:before {
                     border-color: $gray200;
                     background: $gray100;
                     box-shadow: none;
                     cursor: not-allowed;
                 }
                 + label.checkbox__description {
                     color: $gray200;
                     cursor: not-allowed;
                 }
             }
         }
         + label {
             &:empty {
                 &:before {
                     margin-right: 0;
                 }
             }
         }
    }
    .selectWrapper {
        display: block;
        position: relative;
        @include iconMdsp(caretDown);

        &:before {
            display: block;
            font-size: 24px;
            font-size: rem(24px);
            height: 24px;
            height: rem(24px);
            pointer-events: none; //needed so it does not 'capture' click events and passes them through to select
            position: absolute;
            right: 6px;
            right: rem(6px);
            top: 6px;
            top: rem(6px);
            width: 24px;
            width: rem(24px);
            @supports (-moz-appearance:meterbar) { // @supports query to target Firefox browsers specifically: MDS-146210
                top: 3px;
                top: rem(3px);
            }
        }

    }

    .selectWrapper--multiple:before {
        display: none; // needed because the extension below otherwise will be visible on all devices
    }

    // caret down for IOS devices
    @supports (-webkit-overflow-scrolling: touch) {
        .selectWrapper--multiple {
            display: block;
            position: relative;
            @include iconMdsp(caretDown);
            &:before {
                display: inline;
                font-size: 24px;
                height: 24px;
                pointer-events: none;
                position: absolute;
                right: 6px;
                top: 6px;
                width: 24px;
                z-index: 100;
            }
        }
    }

    // caret down for chrome mobile
    @media screen and (-webkit-min-device-pixel-ratio:0) and (min-resolution: .001dpcm) and (hover: none) {
        .selectWrapper--multiple {
            display: block;
            position: relative;
            @include iconMdsp(caretDown);
            &:before {
                display: inline;
                font-size: 24px;
                height: 24px;
                pointer-events: none;
                position: absolute;
                right: 6px;
                top: 6px;
                width: 24px;
                z-index: 100;
            }
        }
    }

    &__select,
    select {
        background-color: $white;
        border-radius: 3px;
        border: 1px solid $gray400;
        box-shadow: 0 1px 2px 0 rgba(0,0,0, 0.24) inset;
        color: $gray800;
        display: block;
        font-family: $siemensFontRegular;
        font-size: 14px;
        font-size: rem(14px);
        line-height: 16px;
        line-height: rem(16px);
        max-width: 100%;
        outline: none;
        overflow: hidden;
        padding: 7px 26px 7px 12px;
        padding: rem(7px 26px 7px 12px);
        width: 100%;

        /* Browser specific stuff */
        appearance: none;
        -moz-appearance: none;
        -webkit-appearance: none;
        box-sizing: border-box;
        -moz-box-sizing: border-box;

        // firefox workaround for a bug which will be fixed in FF, line-height cannot be overwritten
        @supports (-moz-box-sizing: border-box) {
            padding: 6px 26px 6px 12px;
            padding: rem(6px 26px 6px 12px);
        }

        &::-ms-expand { display: none; }

        &:focus {
            box-shadow: 0 0 0px 2px $coolBlue;
            outline: none;
        }

        &:disabled,
        &.is-disabled {
            border-color: $gray200;
            box-shadow: none;
            color: $gray400;

            &:hover {
                cursor: not-allowed;
            }
            option {
                color: $gray400;
            }

        }

        &[multiple] {
            padding: 0;

            option {
                padding: 7px 26px 7px 12px;
                padding: rem(7px 26px 7px 12px);
            }

            // IOS browsers style 'multiple' selects differently
            @supports (-webkit-overflow-scrolling: touch) {
                line-height: 34px;
                padding-left: 12px;
                position: relative;
            }

            // Chrome mobile needs padding whereas the desktop Chrome must not have padding
            // since there is no good way to target chrome only, we use a combination of
            // targeting chrome (first two feature queries) and then only touch deviced (third feature query)
            // hover property: https://developer.mozilla.org/en-US/docs/Web/CSS/@media/hover
            @media screen and (-webkit-min-device-pixel-ratio:0) and (min-resolution: .001dpcm) and (hover: none) {
                padding: 7px 26px 7px 12px;
                padding: rem(7px 26px 7px 12px);
            }
        }
    }

    .inputGroup__textarea {
        border-radius: 3px;
        border: 1px solid $gray400;
        color: $gray800;
        font-family: $siemensFontRegular;
        font-size: 14px;
        font-size: rem(14px);
        line-height: 20px;
        min-width: 300px;
        overflow: auto;
        padding: 8px 12px;
        resize: none;

        &::-webkit-input-placeholder {
            color: $gray400;
        }
        &::-moz-placeholder {
            color: $gray400;
        }
        &:-ms-input-placeholder {
            color: $gray400;
        }

        &:disabled, &.is-disabled,
        &[readonly], &.is-readonly {
            border: 1px solid $gray200;
            box-shadow: none;
        }

        &:disabled, &.is-disabled {
            color: $gray400;
            cursor: not-allowed;
        }
    }

    &__description,
    .radioButton__description,
    .checkbox__description {
        color: $gray400;
        font-family: $siemensFontRegular;
        font-size: 12px;
        font-size: rem(12px);
    }

    .switch {
        &__label {
            color: $gray800;
            cursor: pointer;
            font-size: 14px;
            line-height: 16px;
            margin-left: 3px;
            user-select: none;
        }

        &--s {
            .switch__handle {
                border-radius: 18px;
                height: 24px;
                width: 48px;

                &:before {
                    height: 18px;
                    left: 3px;
                    top: 3px;
                    width: 18px;
                }
            }

            .switch__icon {
                font-size: 18px;
                left: -18px;
                line-height: 18px;
            }

            .switch__checkbox:checked + .switch__handle {
                &:before {
                    transform: translateX(-100%) translateX(-3px);
                }

                .switch__icon {
                    left: 6px;
                }
            }
        }

        &--l {
            .switch__handle {
                border-radius: 32px;
                height: 44px;
                width: 88px;

                &:before {
                    box-shadow: rgba(0, 0, 0, 0.5) 0 0 3px 0;
                    height: 32px;
                    left: 7px;
                    top: 6px;
                    width: 32px;
                }
            }

            .switch__icon {
                font-size: 32px;
                left: -32px;
                line-height: 32px;
            }

            .switch__checkbox:checked + .switch__handle {
                &:before {
                    transform: translateX(-100%) translateX(-7px);
                }

                .switch__icon {
                    left: 10px;
                }
            }
        }

        &__handle {
            border-radius: 24px;
            cursor: pointer;
            display: inline-block;
            font-family: $siemensFontBold;
            height: 32px;
            overflow: hidden;
            position: relative;
            vertical-align: middle;
            user-select: none;
            width: 64px;
            transition: background 0.2s ease-in-out;

            &:before {
                background-color: $white;
                border-radius: 50%;
                box-shadow: rgba(0, 0, 0, 0.5) 0 0 2px 0;
                content: "";
                display: block;
                height: 24px;
                left: 4px;
                position: absolute;
                top: 4px;
                transition: all 0.2s ease-in-out;
                width: 24px;
            }
        }

        &__icon {
            @include iconMdsp(check);

            color: $white;
            font-size: 24px;
            left: -24px;
            line-height: 24px;
            position: absolute;
            text-align: center;
            transform: translateY(-50%);
            transition: left 0.2s ease-in-out;
            top: 50%;
            user-select: none;
        }

        &__checkbox {
            display: none;

            &:not(:checked) {
                & + .switch__handle {
                    background-color: $gray400;
                }
            }

            &:checked {
                & + .switch__handle {
                    &:before {
                        left: 100%;
                        transform: translateX(-100%) translateX(-4px);
                    }

                    .switch__icon {
                        left: 7px;
                    }
                }
            }

            &[disabled] {
                & + .switch__handle {
                    background-color: $gray200 !important;
                    cursor: not-allowed;
                }

                & ~ .switch__label {
                    cursor: not-allowed;
                }
            }
        }
    }
}

.switch__handle {
    // needs to be out of the "switch" scope, so it can get overridden by the bgColor helper classes
    background-color: $stateBlue100;
}

.fieldset {
    border-radius: 3px;
    border: 1px solid $gray200;
    font-size: 14px;
    font-size: rem(14px);
    min-width: 0;
    @include defaultMargin(16px, 0);
    @include defaultPadding();

    .legend {
        font-family: $siemensFontBold;
        font-size: 14px;
        font-size: rem(14px);
        line-height: 20px;
        line-height: rem(20px);
    }
}



//removeIf(sassHandover)
// style native elements when forced defaults
&.#{$forceDefaultClass} {
    textarea {
        @extend .inputGroup__textarea;
    }

    fieldset {
        @extend .fieldset;
        legend {
            @extend .legend;
            float: left;
            padding: 0;

            & + * {
                clear: left;
            }
        }
    }
}
//endRemoveIf(sassHandover)