.list {
    width: 100%;

    .list__header {
        background-color: $white;
        border: 1px solid $gray250;
        border-bottom-width: 0;
        font-family: $siemensFontBold;
        font-size: 14px;
        font-size: rem(14px);
        line-height: 20px;
        line-height: rem(20px);
        margin: 0;
        padding: 16px 14px;
        padding: rem(16px 14px);

        .header__description {
            font-family: $siemensFontRegular;
            margin-top: 4px;
            margin-top: rem(4px);
        }
    }

    .list__list {
        list-style-type: none;
        margin: 0;
        padding-left: 0;
    }

    .list__item {
        align-content: stretch;
        align-items: stretch;
        border: 1px solid $gray250;
        border-bottom-width: 0;
        display: flex;
        flex-direction: row;
        margin: 0;

        &:last-child {
            border-bottom-width: 1px;
        }

        .item__icon {
            align-items: center;
            box-sizing: border-box;
            display: flex;
            flex-basis: auto;
            flex-grow: 0;
            flex-shrink: 0;
            text-align: center;

            .iconMdsp {
                display: inline-block;
                font-size: 24px;
                height: 24px;
                margin-left: 14px;
                width: 24px;
            }
        }

        .item__content {
            align-content: stretch;
            align-items: stretch;
            display: flex;
            flex-basis: auto;
            flex-direction: row;
            flex-grow: 1;
            flex-wrap: wrap;
            justify-content: flex-start;
            margin-bottom: 6px;
            margin-left: 14px;
            margin-top: 6px;

            .content__column {
                flex-grow: 0;
                flex-shrink: 1;
                margin-bottom: 6px;
                margin-top: 6px;

                a.has-icon-only {
                    text-decoration: none;
                }

                &:first-child {
                    .iconMdsp {
                        margin-left: 0;
                    }
                }
            }
        }
    }

    .content__column {
        align-items: center;
        box-sizing: border-box;
        display: flex;
        margin-right: 8px;

        &--primary {
            align-items: flex-start;
            display: flex;
            flex-wrap: wrap;

            .item__title, h4 {
                flex-shrink: 0;
                font-family: $siemensFontBold;
                font-size: 14px;
                font-size: rem(14px);
                line-height: 20px;
                line-height: rem(20px);
                margin-bottom: 4px;
                margin-top: 0;
                width: 100%;
            }
            .item__description {
                color: $gray400;
                flex-shrink: 0;
                font-size: 12px;
                font-size: rem(12px);
                line-height: 16px;
                line-height: rem(16px);
                width: 100%;
            }
        }
        .item__meta {
            color: $gray400;
            font-size: 14px;
            font-size: rem(14px);
            line-height: 20px;
            line-height: rem(20px);
            margin-right: 14px;
        }

        &.has-alignment-right {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: flex-end;
            margin-left: auto;

            a.button, button {
                // to be refined when icon buttons are implemented properly
            }
        }
        &.has-alignment-top {
            align-items: flex-start;
        }
        &.has-alignment-bottom {
            align-items: flex-end;
        }
        &.has-alignment-center {
            align-items: center;
        }
        .iconMdsp {
            font-size: 24px;
            margin-left: 14px;

            &.no-margin-left {
                margin-left: 0;
            }
        }

        // https://code.siemens.com/mds/mds/issues/312 - icon-only buttons container in lists have the wrong margin-left when used without .forceDefaultElements
        .has-icon-only {
            .iconMdsp {
                margin-left: 0;
            }
        }

        // do not delete this: is necessary for correct alignment of badge in list
        .badge {
            vertical-align: top;
        }
    }
}

.list .list__item {
    .xs-hide {
        @include breakpoint($xs) {
            display: none;
        }
    }
    .sm-hide {
        @include breakpoint($sm) {
            display: none;
        }
    }
    .md-hide {
        @include breakpoint($md) {
            display: none;
        }
    }
    .lg-hide {
        @include breakpoint($lg) {
            display: none;
        }
    }
    .xl-hide {
        @include breakpoint($xl) {
            display: none;
        }
    }

    @each $width in $columnWidths {
        .has-default-width-#{$width} {
            flex-basis: calc( #{percentage($width / 100)} - 8px);
        }
    }

    @include breakpoint($xs) {
        @each $width in $columnWidths {
            .xs-#{$width} {
                flex-basis: calc( #{percentage($width / 100)} - 8px);
            }
        }
    }
    @include breakpoint($sm) {
        @each $width in $columnWidths {
            .sm-#{$width} {
                flex-basis: calc( #{percentage($width / 100)} - 8px);
            }
        }
    }
    @include breakpoint($md) {
        @each $width in $columnWidths {
            .md-#{$width} {
                flex-basis: calc( #{percentage($width / 100)} - 8px);
            }
        }
    }
    @include breakpoint($lg) {
        @each $width in $columnWidths {
            .lg-#{$width} {
                flex-basis: calc( #{percentage($width / 100)} - 8px);
            }
        }
    }
    @include breakpoint($xl) {
        @each $width in $columnWidths {
            .xl-#{$width} {
                flex-basis: calc( #{percentage($width / 100)} - 8px);
            }
        }
    }

    // flex wrap utility class to set a list row as no-wrapping row
    .item__content {
        &.xs-nowrap {
            @include breakpoint($xs) {
                flex-wrap: nowrap;
            }
        }
        &.sm-nowrap {
            @include breakpoint($sm) {
                flex-wrap: nowrap;
            }
        }
        &.md-nowrap {
            @include breakpoint($md) {
                flex-wrap: nowrap;
            }
        }
        &.lg-nowrap {
            @include breakpoint($lg) {
                flex-wrap: nowrap;
            }
        }
        &.xl-nowrap {
            @include breakpoint($xl) {
                flex-wrap: nowrap;
            }
        }
    }
}

.list.debug {
    .list__item {
        .item__content {
            background-color: rgba(pink, 0.2);
            .content__column {
                &:nth-child(n+1) {
                    background-color: rgba(turquoise,0.2);
                }
                &:nth-child(n+2) {
                    background-color: rgba(#aab414, 0.2);
                }
                &:nth-child(n+3) {
                    background-color: rgba(#b4854e, 0.2);
                }
                &:nth-child(n+4) {
                    background-color: rgba(#6362b4, 0.2);
                }
                &:nth-child(n+5) {
                    background-color: rgba(#b456a7, 0.2);
                }
                &:nth-child(n+6) {
                    background-color: rgba(#B43E3A, 0.2);
                }
            }
        }
    }
}

