.tabs--primary,
.tabs--secondary {
    list-style: none;

    .tab__nav {
        > ul,
        > div {
            display: flex;
            flex-wrap: wrap;
            list-style-type: none;
            margin: 0;
            padding: 0;
        }
    }

    &.has-alignment-right {
        .tab__nav {
            > ul,
            > div {
                border-left: 1px solid $gray200;
                border-right: none;
                justify-content: flex-end;
            }
        }
    }

    .tab__item {
        display: flex;
        margin: 0;

        a {
            display: flex;
            font-family: $siemensFontBold;
            font-size: 14px;
            font-size: rem(14px);
            line-height: 24px;
            line-height: rem(24px);
            padding: 9px 24px;
            padding: rem(9px 24px);
            text-decoration: none;

            .iconMdsp {
                font-size: 24px;
                font-size: rem(24px);
                padding-right: 4px;
                padding-right: rem(4px);
            }
        }
    }

    .tab__content {
        padding: 16px;
        padding: rem(16px);
    }
}

.tabs--primary {
    background-color: transparent;

    .tab__nav {
        > ul {
            background-color: $gray100;
            border-right: 1px solid $gray200;
            border-top-left-radius: 4px;
            border-top-right-radius: 4px;
            border-top: 1px solid $gray200;
        }
    }

    .tab__content {
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
        border: 1px solid $gray200;
    }

    .tab__item {
        margin-top: -1px;



        &:first-child > a {
            border-left: 1px solid $gray200;
            border-top-left-radius: 4px;
        }

        a {
            background-color: $gray100;
            border-right: 1px solid $gray200;
            border-top: 1px solid $gray200;
            box-sizing: border-box;
            color: $stateBlue100;
            float: left; // needed for IE 11

            &.is-activated {
                background: $white;
                border-bottom: 1px solid $white;
                color: $gray800;
                margin-bottom: -1px;
                &:hover {
                    border-bottom: 1px solid $stateBlue;
                }
            }

            &:active {
                background: $white;
                color: $gray800;
            }

            &:focus {
                box-shadow: inset 0 0 0 2px $coolBlue;
                position: relative;

                // IE11 does not support outline-offset, hence the feature query
                outline: 0;

                @supports (outline-offset: -2px) {
                    box-shadow: none;
                    outline-offset: -2px;
                    outline: 2px solid $coolBlue;
                }
            }

            &:hover {
                background: $stateBlue;
                color: $stateBlue100;
            }
        }
    }

    // different possible breakpoints for single col tabs
    @mixin singleColPrimary($breakpoint) {
        @include breakpoint($breakpoint) {
            .tab__nav {
                > ul {
                    border-right-width: 0;
                }
            }

            .tab__item {
                display: block;
                width: 100%;

                &:first-child > a {
                    border-top-left-radius: 0;
                }
                a {
                    border-left: 1px solid $gray200;
                    float: none;
                    width: 100%;
                }
            }
        }
    }

    & {
        @include singleColPrimary($xs);
    }
    &.sm {
        @include singleColPrimary($sm);
    }
    &.md {
        @include singleColPrimary($md);
    }
    &.lg {
        @include singleColPrimary($lg);
    }



    &.tabs--frameless {
        .tab__nav {
            > ul {
                background-color: transparent;
                border-top: none;
                border-right: none;
                padding: 0 0 0 32px;
                padding: rem(0 0 0 32px);

                @include breakpoint($xs) {
                    padding: 0 32px 0 32px;
                    padding: rem(0 32px 0 32px);
                }
            }
        }

        .tab__item {
            margin-top: -1px;

            &:last-child > a {
                border-left: 1px solid $gray200;
                border-top-right-radius: 4px;
            }
        }

        .tab__content {
            border: 0;
            border-top: 1px solid $gray200;
            padding: 16px 32px;
            padding: rem(16px 32px);
        }

        &.sm {
            .tab__nav > ul {
                @include breakpoint($sm) {
                    padding: 0 32px 0 32px;
                    padding: rem(0 32px 0 32px);
                }
            }
        }

        &.md {
            .tab__nav > ul {
                @include breakpoint($md) {
                    padding: 0 32px 0 32px;
                    padding: rem(0 32px 0 32px);
                }
            }
        }

        &.lg {
            .tab__nav > ul {
                @include breakpoint($lg) {
                    padding: 0 32px 0 32px;
                    padding: rem(0 32px 0 32px);
                }
            }
        }
    }

}

.tabs--secondary {
    .tab__item a {
        border-bottom: 1px solid $gray200;
        box-sizing: border-box;
        color: $stateBlue100;

        &.is-activated {
            border-bottom: 2px solid $gray800;
            color: $gray800;
        }

        &:active {
            border-bottom: 2px solid $gray800;
            color: $gray800;
        }

        // todo: replace dotted focus states with finalized states
        &:focus {
            border-bottom-color: transparent !important;
            box-shadow: inset 0 0 0 2px $coolBlue;

            // IE11 does not support outline-offset, hence the feature query
            outline: 0;
            @supports (outline-offset: -2px) {
                box-shadow: none;
                outline-offset: -2px;
                outline: 2px solid $coolBlue;
            }
        }

        &:hover {
            border-bottom: 2px solid $stateBlue100;
            color: $stateBlue100;
        }
    }

    // different possible breakpoints for single col secondary tabs
    @mixin singleColSecondary($breakpoint) {
        @include breakpoint($breakpoint) {
            .tab__item {
                display: block;
                width: 100%;
                a {
                    border-bottom-width: 2px;
                    display: block;
                    width: 100%;
                }
            }

        }
    }

    & {
        @include singleColSecondary($xs);
    }
    &.sm {
        @include singleColSecondary($sm);
    }
    &.md {
        @include singleColSecondary($md);
    }
    &.lg {
        @include singleColSecondary($lg);
    }
}

.main__region { // in context of detail view area
    > .tabs--frameless,
    > .tab__content--frameless {
        margin-left: -32px;
        margin-right: -32px;
    }
}
