// 8
@font-face {
    font-family: "Siemens Sans Black";
    font-display: auto;
    src: url("#{$assetPath}#{$webfontsPath}sisan08.eot");
    src: url("#{$assetPath}#{$webfontsPath}sisan08.eot#iefix") format("embedded-opentype"),
    url("#{$assetPath}#{$webfontsPath}sisan08.woff2") format("woff2"),
    url("#{$assetPath}#{$webfontsPath}sisan08.woff") format("woff"),
    url("#{$assetPath}#{$webfontsPath}sisan08.ttf") format("truetype");
    font-weight: normal;
    font-style: normal
}

