.form {
    &__formGroup.has-error {
        margin-bottom: 16px;
        margin-bottom: rem(16px);

        &:before {
            background-color: $functionalRed;
            content: '';
            display: block;
            bottom: 0;
            left: -10px;
            position: absolute;
            top: 0;
            width: 2px;
        }

        > .inputGroup.is-invalid {
            > .checkboxWrapper > label,
            > .selectWrapper > select,
            > .radioButtonWrapper > label,
            > input,
            > textarea {
                border-color: $functionalRed;
                color: $functionalRed;

                &::-webkit-input-placeholder {
                    color: $functionalRed;
                }
                &::-moz-placeholder {
                    color: $functionalRed;
                }
                &:-ms-input-placeholder {
                    color: $functionalRed;
                }

                &[class$="__description"] {
                    color: $gray400;
                }
            }
        }
    }

    &__requiredMsg, .inputGroup--error {
        color: $functionalRed;
        font-family: $siemensFontRegular;
        font-size: 13px;
        font-size: rem(13px);
        line-height: 17px;
        line-height: rem(17px);
        margin-top: 2px;
        margin-top: rem(2px);
    }

    .inputGroup--error {
        cursor: pointer;
        @include iconMdsp(arrowGoTo, after);

        &:after {
            color: $functionalRed;
            font-size: 16px;
            font-size: rem(16px);
            line-height: 13px;
            line-height: rem(13px);
        }

        // inserts a line break before every >1th validation error label
        &:nth-child(n+2):before {
            content: "\A";
            white-space: pre;
        }
    }

    &__buttons {
        margin-top: 8px;
        margin-top: rem(8px);
    }

    .messageWrapper {
        padding: 12px 0 24px 0;
        padding: rem(12px 0 24px 0);
    }
}